.container {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form_wrapper {
    display: flex;
    flex-direction: column;
    display: inline-block;
    transform: translateY(35%);
}

.wrapper_header {
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1920px;
  width: 100%;
  padding: 0 4rem 0 3rem;
  position: relative;
}

.wrapper_header button {
  color: black;
  background: none;
  border: 0;
  padding: 0;
  display: inline-block;
  cursor: pointer;
  height: 2rem;
  color: white;
  transition: all 0.15s ease-in-out;
}

.wrapper_header h2 {
  font-size: 2rem;
  font-weight: 400;
  color: #feffb3;
  padding: 1rem 0 1.25rem 0rem;
  line-height: 1.15;
  cursor: pointer;
}

.tittle {
    font-size: 2rem;
    font-weight: 400;
    line-height: 2.5rem;
    color: #FEFFB3;
    margin-bottom: 3rem;
    text-align: start;
}

.input {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    min-height: 5rem;
    height: 100%;
    max-width: 45.5rem;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 0 1.5rem 0 1.5rem;
}

.input_wrapper {
    position: relative;
    width: 45.5rem;
}

.input::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
    padding: 2rem 0 2rem 0;
}

.btn_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btn {
    padding: 1rem 7rem 1rem 7rem;
    font-size: 1.5rem;
    font-weight: 200;
    line-height: 1.75rem;
    background: transparent;
    border: 2px solid #FBE0F3;
    color: #FBE0F3;
    cursor: pointer;
    letter-spacing: .25rem;
    display: flex;
    align-items: center;
    border-radius: 100rem;
    margin-top: 1.5rem;
    position: relative;
    gap: 1.5rem;
}

.toggle {
    position: absolute;
    top: 50%;
    right: 1.5rem;
    transform: translateY(-50%);
    width: 2rem;
    height: 2rem;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
}


.input_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 0.5rem;
}

.error {
  color: #eb443c;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out;
  font-size: 1rem;
  padding: 0;
  margin: 0;
  font-weight: 400;
  letter-spacing: 4px;
  opacity: 0;
  text-align: center;
  line-height: 1.5rem;
  white-space: pre-line;
}

.visible {
  opacity: 1;
  max-height: 3rem; 
}

.btn_wrapper .error {
  position: relative;
  transform: none;
  margin-top: 1rem;
}

.error_border {
    border-color: rgba(201, 42, 21, 0.85);
}

@media (max-width: 768px) {
  .wrapper_header {
    padding: 0 1.5rem;
    margin: 1.5rem 0 2.5rem 0;
  }
  .wrapper_header h2 {
    position: absolute;
    font-size: 1.25rem;
    line-height: 1;
    padding: 0;
    margin: 0;
    transform: translateX(50%);
    right: 50%;
  }

  .wrapper_header button svg {
    width: 1rem;
    height: 1rem;
  }

  .wrapper_header button {
    margin-left: auto;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .form_wrapper {
    transform: none;
    width: 100%;
    padding: 0 1rem;
  }

  .tittle {
    font-size: 1.5rem;
    line-height: 1;
    margin-bottom: 2rem;
  }

  .input_wrapper {
    max-width: 100%;
    width: 100%;
  }   

  .input::placeholder {
    font-size: .75rem;
    letter-spacing: 2px;
  }   

  .input {
    border: 1px solid #fbe0f3;
    height: 100%;
    max-width: none;
    width: 100%;
    font-size: .75rem;
    color: #fbe0f3;
    padding: 1rem;
    min-height: 0;
  }

  .btn {
    padding: .5rem 2rem;
    font-size: .75rem;
    line-height: 1;
    letter-spacing: 2px;
    gap: .5rem;
    border: 1px solid #FBE0F3;
    margin-top: 1rem;
  }

  .btn svg {
    width: .5rem;
    height: .5rem;
  }

  .toggle img {
    width: 1rem;
    height: 1rem;
  }

  .toggle {
    right: .5rem;
    width: 1rem;
    height: 1rem;
  }

  .form {
    gap: 1rem;
  }
}